.filter-platform{
    font-family: Jokerman, sans-serif;
    margin-right: auto;
    margin-left: auto;
}

.filter-platform > button{
    font-family: Jokerman, sans-serif;
    font-size: x-large;
    color: black;
    border-radius: 20px;
    width: 200px;
    margin: 50px;
}

.movie-card{
    width: 18rem;
    font-family: "Times New Roman", serif;
    font-size: large;
    background: white;
    color: black;
    border-radius: 20px;
    padding: 20px;
    margin: 20px;
}

.movie-card-grid{
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-evenly;
}

@media only screen and (max-width: 480px){
    .movie-card-grid{
        display: grid;
        grid-template-columns: auto;
        justify-content: space-evenly;
    }
}

.movie-card > img{
    cursor: pointer;
}